import "./styles.css";
import React, { useState } from "react";

const Calculator = () => {
  const { result, setResult } = useState("");

  const handleClick = (e) => {
    setResult(result.concat(e.target.value));
  };

  const backspace = () => {
    setResult(result.slice(0, result.length - 1));
  };

  const calculate = () => {
    try {
      setResult(eval(result).toString());
    } catch {
      setResult("Error");
    }
  };

  return (
    <div id="app">
      <div className="calculator">
        <div className="formulaScreen"></div>
        <div className="outputScreen" id="display">
          <form>
            <input id="result" type="text" value={result} />
          </form>
        </div>
        <div>
          <button className="jumbo" onClick={backspace} id="clear" value="AC">
            AC
          </button>
          <button onClick={handleClick} id="divide" value="/">
            /
          </button>
          <button onClick={handleClick} id="multiply" value="x">
            x
          </button>
          <button onClick={handleClick} id="seven" value="7">
            7
          </button>
          <button onClick={handleClick} id="eight" value="8">
            8
          </button>
          <button onClick={handleClick} id="nine" value="9">
            9
          </button>
          <button onClick={handleClick} id="subtract" value="-">
            -
          </button>
          <button onClick={handleClick} id="four" value="4">
            4
          </button>
          <button onClick={handleClick} id="five" value="5">
            5
          </button>
          <button onClick={handleClick} id="six" value="6">
            6
          </button>
          <button onClick={handleClick} id="add" value="+">
            +
          </button>
          <button onClick={handleClick} id="one" value="1">
            1
          </button>
          <button onClick={handleClick} id="second" value="2">
            2
          </button>
          <button onClick={handleClick} id="three" value="3">
            3
          </button>
          <button onClick={handleClick} className="jumbo" id="zero" value="0">
            0
          </button>
          <button onClick={handleClick} id="decimal" value=".">
            .
          </button>
          <button onClick={calculate} id="equals" value="=">
            =
          </button>
        </div>
      </div>
      <div className="author">
        Designed and Coded By
        <br />
        <p>Peter Weinberg</p>
      </div>
    </div>
  );
};

export default Calculator;
